/* Main Components Most of Styling*/
/* Nest 1
slide-nav
slide-navigation-wrapper
*/
.slide-nav {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  display: block;
  background: -webkit-linear-gradient(steelblue 10%, darkviolet);
  background: linear-gradient(to   , darkviolet);
  visibility: hidden; }
  .slide-nav .slide-navigation-wrapper {
    height: 100%;
    overflow-y: auto;
    overflow-scrolling: touch;
    padding: 40px 5% 40px calc(10% + 80px);
    -webkit-transition: translateZ(0);
    -moz-transition: translateZ(0);
    transition: translateZ(0);
    backface-visibility: hidden;
    -webkit-transform: translateX(-50%);
    -moz-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    -o-transform: translateX(-50%);
    transform: translateX(-50%);
    -webkit-transition-timing-function: cubic-bezier(0.86, 0.01, 0.77, 0.78);
    -moz-transition-timing-function: cubic-bezier(0.86, 0.01, 0.77, 0.78);
    transition-timing-function: cubic-bezier(0.86, 0.01, 0.77, 0.78); }
  .navigation-is-open .slide-nav {
    visibility: visible; }
    .navigation-is-open .slide-nav .slide-navigation-wrapper {
      -webkit-transform: translateX(0);
      -moz-transform: translateX(0);
      -ms-transform: translateX(0);
      -o-transform: translateX(0);
      transform: translateX(0);
      -webkit-transition: 0.5s;
      -moz-transition: 0.5s;
      transition: 0.5s; }

.slide-nav, .animate-opacity {
  opacity: 0; }

.animate-opacity {
  -webkit-transition: opacity 0.8s ease;
  -moz-transition: opacity 0.8s ease;
  transition: opacity 0.8s ease;
  opacity: 1; }

/* Nest 2
#title <h2>
slide-nav <div>
*/
#navTitle {
  font-family: "Lato", sans-serif;
  position: relative;
  font-size: 1.61803em;
  margin-top: 2.7em;
  padding-left: 20px;
  margin-top: 0em; }
  #navTitle .slide-nav::after {
    position: absolute;
    left: 0;
    bottom: -20px;
    height: 1px; }

/* Nest 3
 slide-primary-nav <ul>
 links  <a>
*/
.slide-nav .slide-primary-nav {
  margin-top: 5em; }
  .slide-nav .slide-primary-nav li {
    list-style: none;
    margin: 1.6em 0; }
  .slide-nav .slide-primary-nav a {
    border: none;
    border-bottom: 2px solid blue;
    background: -webkit-linear-gradient(cyan, cyan) no-repeat bottom;
    background: -o-linear-gradient(cyan, cyan) no-repeat bottom;
    background: linear-gradient(cyan, cyan) no-repeat bottom;
    background-size: 100% 1px;
    transition: .2s,color .1s .1s;
    text-decoration: none;
    font-size: 1.61803em;
    margin-left: -20px;
    font-family: "Lato", sans-serif;
    color: rgba(255, 255, 255, 0.5);
    display: inline-block; }
    .slide-nav .slide-primary-nav a.selected {
      color: cyan; }
    .slide-nav .slide-primary-nav a:hover {
      color: #fff5ee; }

