@import "modules/projects-page/projects-module"; // animation
@import "modules/global-mqs/mqs"; // media q
@import "partials/variables";
$allrects:rectangle;

.#{$allrects}.display{
    visibility: hidden;
}

.halfCircle,.halfCircle1 {
    @include tablet {
        display:none;
     }
}
// btm-rect-dreaming, .top-rect-dreaming
.#{$allrects}.display{
    @include tablet {
        visibility:visible;;
    }
}
.rect-divide{
    @include mobile_device {
        height:1.5px;
    }
}

// translate halfCircle on y axis 400px when hovered on
.projects:hover .halfCircle{
    @include halfCircle-animation-on-hover;
}

// translate halfCircle on y axis back to 250px
.halfCircle{
    @include halfCircle-animation-off-hover;
}

// halfCircle2 fades in when hoverd on
.projects:hover .halfCircle1{
    visibility: visible;
    opacity: 0.8;
    @include fadein-halfCircle1-on-hover;
}

// halfCircle1 fades out
.halfCircle1{
    visibility: hidden;
    opacity: 0;
    @include transition(visibility 1s, opacity 1s);
}
// hover over projects svg and view now text slides up on y axis
.projects:hover #view{
    @include viewNowUp;
}

// when user hovers off projects view now slides down
#view{
    @include viewNowUnder;
}

#project-name{
    @include projectName-slideUp;
}

.projects:hover #project-name{
    @include projectName-slideUnder;
}

.projects:hover .link{
    fill:black;
}
